.container
    {
        position: absolute;
        left: 57%;
        top: 23%;
        transform: translate(-50% , -50%);
    }

   .cube
    {
        background: rgb(78, 83, 83);
        width: 900px;
        height: 60px;
        position: relative;
        margin: 50px;
        display: flex;
        flex-direction: 1 column;
    }

   .cube::before
    {
        content: '';
        display: inline-block;
        background: rgb(117, 124, 124);
        width:900px;
        height: 20px;
        transform: skewX(-40deg);
        position: absolute;
        top: -20px;
        left: 8px;
    }

   .cube::after
    {
        content: '';
        display: inline-block;
        background: rgb(47, 51, 51);
        width: 16px;
        height: 60px;
        transform: skewY(-50deg);
        position: absolute;
        top: -10px;
        left: 100%;
    }
